







import {Component, Prop, Vue} from 'vue-property-decorator';

@Component
export default class CvFacebookShare extends Vue {
    @Prop() albumURL!: string;

    fbSharing(){
        // @ts-ignore
        // eslint-disable-next-line no-undef
        FB.ui({
            method: 'share',
            href: this.albumURL
        });
    }
}
